body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  color: #edf5e1;
  background: #5cdb95;
}

.nav {
  position: absolute;
  width: 200px;
  top: 30px;
  right: 30px;
  z-index: 10; }
  .nav .nav-item {
    flex: 1 1;
    align-items: flex-end;
    padding: 10px 0;
    text-align: center;
    transition: box-shadow 0.5s;
    will-change: transform;
    margin: 10px;
    font-weight: bold;
    background: #05386b; }
    .nav .nav-item:hover {
      box-shadow: 0px 3px 10px -1px rgba(0, 0, 0, 0.4); }

@media screen and (max-width: 900px) {
  .nav {
    position: absolute;
    width: 100px;
    top: 30px;
    right: 5px;
    z-index: 10; }
    .nav .nav-item {
      flex: 1 1;
      align-items: flex-end;
      padding: 10px 0;
      text-align: center;
      transition: box-shadow 0.5s;
      will-change: transform;
      margin: 10px;
      font-weight: bold;
      background: #05386b; }
      .nav .nav-item:hover {
        box-shadow: 0px 3px 10px -1px rgba(0, 0, 0, 0.4); } }

.page {
  position: absolute;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  font-weight: 100;
  font-size: 3em;
  will-change: transform, opacity;
  text-shadow: 0px 2px 40px #00000020, 0px 2px 5px #00000030; }

.social-link {
  position: absolute;
  bottom: 20px;
  right: 30px; }
  .social-link .sl-item {
    font-size: 25px;
    margin: 2px;
    padding: 5px;
    border-radius: 50%;
    color: #05386b;
    cursor: pointer;
    transition: all 0.4s ease; }
    .social-link .sl-item:hover {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      background-color: #ffffff; }
  .social-link .github:hover {
    color: #333333;
    border-color: #333333; }
  .social-link .linkedin:hover {
    color: #0077b5;
    border-color: #0077b5; }
  .social-link .stack:hover {
    color: #f48024;
    border-color: #f48024; }
  .social-link .twitter:hover {
    color: #1da1f2;
    border-color: #1da1f2; }

.header {
  text-align: center;
  font-weight: 800;
  font-size: 5vw;
  margin-top: 5%; }

.info {
  font-size: 2vw;
  padding: 2%;
  width: 75%;
  color: black; }

@media screen and (max-width: 900px) {
  .header {
    text-align: center;
    font-weight: 800;
    font-size: 9vw;
    margin-top: 5%; }
  .info {
    font-size: 3.2vw;
    padding: 2%;
    width: 70vw;
    color: black; } }

.form-row {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: 20px 44px 1px 44px; }

.form-row .name {
  width: 188px;
  color: #333;
  font-size: 15px;
  font-weight: 700;
  margin-top: 33px; }

.form-row .value {
  width: calc(100% - 188px); }

button {
  outline: none;
  background: none;
  border: none; }

.page-wrapper {
  min-height: 100vh; }

h2 {
  font-size: 30px; }

.wrapper {
  margin: 0 auto;
  max-width: 900px; }

.btn {
  line-height: 50px;
  padding: 0 30px;
  cursor: pointer;
  font-size: 15px;
  font-weight: 700;
  color: #fff;
  border-radius: 5px;
  background: #2c6ed5; }

input,
textarea {
  outline: none;
  margin: 0;
  border: none;
  box-shadow: none;
  width: 60%;
  font-size: 14px;
  font-family: inherit; }

textarea {
  resize: none; }

.inputStyle {
  background: transparent;
  line-height: 38px;
  border: 1px solid #cccccc;
  border-radius: 3px;
  color: #666;
  font-size: 15px;
  transition: all 0.4s ease;
  padding: 0 20px; }

.inputStyle:focus {
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.15);
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px); }

.title {
  font-size: 36px;
  font-weight: 700;
  text-align: left;
  color: #fff;
  margin-bottom: 24px; }

.form {
  border-radius: 3px;
  background: #fff;
  background: transparent; }

.form-d .form-heading {
  background: transparent; }

.form-d .form-body {
  background: #fff;
  position: relative;
  border: 1px solid #e5e5e5;
  border-bottom: none;
  padding: 30px 0;
  padding-bottom: 0;
  border-top-right-radius: 3px; }

.form-d .form-body:before {
  bottom: 100%;
  left: 75px;
  border: solid transparent;
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  border-color: rgba(255, 255, 255, 0);
  border-bottom-color: #fff;
  border-width: 10px; }

.form-d .footer {
  background: #fff;
  border: 1px solid #e5e5e5;
  border-top: none;
  padding: 20px 30px; }

@media (max-width: 900px) {
  .title {
    padding: 0 15px;
    font-size: 9vw; }
  .form-d .form-body {
    background: #fff;
    position: relative;
    border-bottom: none;
    padding: 5px 0;
    width: 5em;
    left: 4%; }
  .name {
    margin-top: 1.4em !important; }
  .form-row {
    display: block;
    padding: 0px 0px 0px 25px; }
    .form-row .name,
    .form-row .value {
      display: block;
      width: 100%; }
  .footer {
    padding: 0px 15px !important; } }

.main-container {
  margin: 10px; }

.headerData {
  font-weight: 800; }

.mainData {
